.auth{
    max-width: 100%;
    min-height: 100vh;
    background: rgb(7, 116, 122);
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 3rem;
}

.form{
    width: 38%;
    height: 25rem;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    background: rgb(3, 56, 59);
    box-shadow:0 1rem 2rem #111;
    transition: all 4s ease;
}

.auth_input{
    width: 70%;
    outline: none;
    height: 4rem;
    border: none;
    border-bottom: 1px solid #fff;
    border-radius: 6px;
    background: rgb(3, 56, 59);
    color:#fff;
    font-family: Georgia, 'Times New Roman', Times, serif;
}
.auth_input:focus{
    outline: none;
    border: none;
    border-bottom: 1px solid rgb(7, 116, 122);
}
.sinein{
    padding: .5rem 1rem;
    font-size: 1.6rem;
    border-radius: 6px;
    font-family: Georgia, 'Times New Roman', Times, serif;
    background: rgb(3, 56, 59);
    box-shadow: 0 .4rem .6rem #111;
    outline: none;
    color: rgb(187, 184, 184);
    border: 1px solid rgb(7, 116, 122);
}
.sineup{
    padding: .5rem 1rem;
    font-size: 1.6rem;
    border-radius: 6px;
    font-family: Georgia, 'Times New Roman', Times, serif;
    background: rgb(3, 56, 59);
    box-shadow: 0 .4rem .6rem #111;
    outline: none;
    color: rgb(187, 184, 184);
    border: 1px solid rgb(7, 116, 122);
}
.sineup:disabled{
    background: rgb(99, 98, 98);
    box-shadow: none;
    cursor: not-allowed
}
.sineup:disabled:hover{
    background: rgb(99, 98, 98)
}
.sineup:disabled:no-button
.sinein:hover{
    background: rgb(8, 114, 120)
}
.sineup:hover{
    background: rgb(8, 114, 120)
}
.sineup:active{
    border: 1px solid rgb(4, 52, 54)
}
.valid{
    width: 70%;
    outline: none;
    height: 4rem;
    border: none;
    border-bottom: 1px solid #fff;
    border-radius: 6px;
    background: rgb(59, 3, 33);
    color:#fff;
    font-family: Georgia, 'Times New Roman', Times, serif;
}
.valid:focus{
    outline: none;
    border: none;
    border-bottom: 1px solid rgb(226, 24, 51);
}
@media (max-width:1000px){
    .auth{
        flex-direction: column;
    }
    .form{
        width:65%;
    }
}
@media (max-width:550px){
   
    .form{
        width:80%;
        height: 20rem;
        margin: 4rem 0;
    }
    .sinein{
        margin-bottom: 2rem;
    }
    .sineup{
        margin-bottom: 2rem;
    }
}